<script>
  let clicked = false;
  let login = false;
  let loginToggle;
  $:{
     loginToggle = login ? 'Dont have an account?' : 'Already have an account?';
  }

  let emailInput;
  let passwordInput;
  let nameinput;

  $: {
    console.log(`${clicked}`);
  }
  function signUp() {

    var data = {
      email: emailInput,
      password: passwordInput,
      name: nameinput
    };
    fetch("/signup", {
      method: "POST",
      body: data
    })
      .then(response => response.json())
      .then(data => {
        console.log("Success:", data);
      })
      .catch(error => {
        console.error("Error:", error);
      });
  }

  function signIn() {
    var data = {
      email: emailInput,
      password: passwordInput
    };
    fetch("/login", {
      method: "POST",
      body: data
    })
      .then(response => response.json())
      .then(data => {
        console.log("Success:", data);
      })
      .catch(error => {
        console.error("Error:", error);
      });
  }
</script>

<button
  on:click={() => (clicked ? (clicked = false) : (clicked = true))}
  class="fixed-button">{clicked ? 'close' : 'Sign up/Login'}</button>
<div class:fadein={clicked} class="form-container">
  <form class="form-signin">
    <h1 class="headline-primary">{login ? 'Beta Login': 'Beta Signup'}</h1>
  <button class="toggle-button" on:click={() => (login ? (login=false):(login=true))}>{loginToggle}</button>
    <label for="email" class="">Email address</label>
    <input
      type="email"
      id="email"
      class=""
      placeholder="Email address"
      required />
    <label for="password" class="">Password</label>
    <input
      type="password"
      id="password"
      class=""
      placeholder="Password"
      required />
    <label for="name" class:fadeout={login} class="">Name</label>
    <input type="text" class:fadeout={login} id="name" class="" placeholder="Name" required />
    <button on:click={()=> login ? (signIn()) : (signUp())} class="button-signin">{login ? "Jack in" : "Sign up!"}</button>
  </form>
</div>


